import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import {CommonPages} from './common.component';


export const routes: Routes = [
    {
        path: '',
        component: CommonPages,
        children: [
            { path: 'base-manage', loadChildren: './base-manage/base-manage.module#BasekManageModule' }
        ]
    }
];

export const CommonRouting: ModuleWithProviders = RouterModule.forChild(routes);
